// Imports for Google Fonts
@import url( 'https://fonts.googleapis.com/css?family=Lora:700|Roboto+Slab|Roboto:400,700'); //Default font stacks

%serif {
    font-family: Javanese, Georgia, 'Times New Roman', serif;
}

%serif-secondary {}

%sans-serif {
    font-family: Roboto, Arial, 'Helvetica', sans-serif;
}

%sans-serif-secondary {
    font-family: 'Roboto Slab', Courier, Arial, Helvetica, sans-serif;
}

html {
    @extend %sans-serif;
    font-size: 16px;
    line-height: 1.4;
}

.content-area {
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        @extend %sans-serif-secondary;

        & + p {
            margin-top: 0;
        }
    }

    h1 {
        color: color(primary);
        font-size: 2em;
    }

    h2 {
        color: color(gray);
        font-size: 1.5em;
    }

    h3 {
        color: color(primary);
        font-size: 1.17em;
    }

    h4 {
        color: color(secondary);
        font-size: 1em;
    }

    h5 {
        color: color(tertiary);
        font-size: 0.87em;
    }

    h6 {
        color: color(gray);
        font-size: 0.67em;
    }

    p {
        margin: 0 0 1.5em;
        @extend %sans-serif;
    }

    figcaption {
        font-style: italic;
        font-size: 0.8125em;
        text-align: center;
        padding: 0.5rem;
    }

    blockquote {
        font-style: italic;
        margin-left: 0;
        margin-right: 0;

        p {
            color: color(primary);
            font-size: 1.125em;
        }
    }

    a {
        color: color(primary);
        text-decoration: none;
        padding-bottom: 2px;
        border-bottom: 1px solid color(primary);

        &:hover {
            color: color(primary-dark);
            border-bottom: 1px solid color(primary-dark);
        }
    }
    //List style - colored bullets and numbers

    ul {
        list-style: none;
        padding-left: 1em;
        padding-bottom: 4px; //Fixes link underline being clipped from "overflow: hidden;" when the last list item contains a link.
        color: color(primary);

        .site-content & {
            overflow: hidden;
        }

        li {
            margin-bottom: 6px;
            line-height: 1.6;

            &:before {
                content: '\25A0';
                margin-right: 0.35em;
            }
        }
    }

    ol {
        padding-left: 1em;
        list-style: none;
        counter-reset: li;
        color: color(primary);

        li {
            counter-increment: li;
            line-height: 1.6;

            ul {
                list-style: none;

                li {
                    counter-increment: unset;

                    &:before {
                        content: '\25A0';
                        margin-right: 0.35em;
                    }
                }
            }
        }

        li:before {
            content: counter(li) '. ';
            font-weight: bolder;
        }
    }
}

.logo {
    @extend %serif;
}
